<template>
  <div class="edit-identity-status">
    <el-dialog
      :visible="isShow"
      :title="$t('Edit Identity Status')"
      width="448px"
      top="29vh"
      :close-on-click-modal="false"
      @submit.prevent.native
      :before-close="closeService"
    >
      <!-- <el-divider /> -->
      <el-form label-position="top" ref="editStatusFormRef" :model="submitData" :rules="rules">
        <el-form-item>
          <div class="cis">Current Identity Status</div>
          <div class="sta">{{ identityStatus }}</div>
        </el-form-item>
        <el-form-item :label="$t('New Identity Status')" prop="status" required>
          <el-select v-model="submitData.status" :placeholder="$t('common.placeholder.select')">
            <el-option
              v-for="(opt, index) in statusOptions"
              :key="index"
              :value="opt.value"
              :label="opt.label"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Remark" prop="remark" required>
          <el-input
            type="textarea"
            prop="remark"
            :rows="5"
            resize="none"
            v-model="submitData.remark"
            placeholder="Please enter "
          />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" plain @click="closeService()">{{ $t('common.button.cancel') }}</el-button>
        <el-button type="primary" v-preventReClick size="small" @click="submitService()">{{
          $t('common.button.submit')
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { inputRequiredValidateObj } from '@/common/validator/common';
import { changeStatus } from '../../../api/api-customer';

export default {
  name: 'editIdentityStatus',
  props: {
    closeDialog: {
      type: Boolean,
      default: false,
    },
    closeData: {
      type: Object,
    },
    identityStatus: {
      type: String,
      default: '',
    },
    identityId: {
      type: String,
      default: '',
    },
    statusOptions: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      rules: {
        remark: [inputRequiredValidateObj],
      },
      isShow: this.closeDialog,
      submitData: {
        status: '',
        remark: '',
      },
    };
  },
  watch: {
    closeDialog(val) {
      this.isShow = val;
    },
    statusOptions: {
      handler(val) {
        if (val.length > 0) {
          this.submitData.status = val[0].value;
        }
      },
      deep: true,
    },
  },
  computed: {},
  created() {},
  methods: {
    showStatus(val) {
      switch (val) {
        case 'Active':
          return '03';
        case 'Closed':
          return '06';
        case 'Capped':
          return '07';
        case 'New':
          return '00';
        case 'Pending Active':
          return '02';
        case 'Suspended':
          return '04';
        case 'Frozen':
          return '05';
        case 'Dormant':
          return '08';
        default:
          return;
      }
    },
    closeService() {
      this.$refs.editStatusFormRef.resetFields();
      this.isShow = false;
      this.$emit('ishow', false);
    },
    submitService() {
      this.$refs['editStatusFormRef'].validate((valid) => {
        if (valid) {
          let params = {
            oldStatus: this.showStatus(this.identityStatus),
            newStatus: this.submitData.status,
            remark: this.submitData.remark,
            receiverIdentityId: this.identityId,
          };
          changeStatus(params).then((res) => {
            if (res.header && res.header.success) {
              this.$modal.msgSuccess(`Status edited successfully.`);
              this.closeService();
              this.$emit('closeSubmit');
            }
          });
        }
      });
    },
  },
  mounted() {},
};
</script>
