import {
  inputRequiredValidateObj,
  msisdnValidateObj,
  len0To32StrValidateObj,
  nonSpecialCharValidateObj,
  len256StrValidateObj,
  selectRequiredValidateObj,
  nonSpecialCharExceptPercentSignValidateObj,
  len0To18StrValidateObj,
  checkLengthExceptPercentSign,
} from '@/common/validator/common';

let checkInputValidate = () => {
  return {
    trigger: 'blur',
    validator: async (rule, value, callback) => {
      if (value) {
        callback();
      } else {
        callback(new Error('Please enter content.'));
      }
    },
  };
};

const getDataFormRules = () => {
  return {
    invitationCode: [nonSpecialCharValidateObj],
  };
};
const customerMutiCondiQueryRules = (scope) => {
  return {
    condition: [selectRequiredValidateObj],
    msisdn: [
      inputRequiredValidateObj,
      nonSpecialCharExceptPercentSignValidateObj,
      msisdnValidateObj,
      len0To18StrValidateObj,
    ],
    middleName: [
      len0To32StrValidateObj,
      nonSpecialCharExceptPercentSignValidateObj,
      checkLengthExceptPercentSign('This filed is mandatory'),
    ],
    idType: [selectRequiredValidateObj],
    idNumber: [checkInputValidate, len256StrValidateObj],
    birthday: [selectRequiredValidateObj],
    customerType: [selectRequiredValidateObj],
    customerId: [checkInputValidate(scope), len0To32StrValidateObj],
    firstName: [nonSpecialCharExceptPercentSignValidateObj, len0To32StrValidateObj],
    lastName: [nonSpecialCharExceptPercentSignValidateObj, len0To32StrValidateObj],
  };
};

export { getDataFormRules, customerMutiCondiQueryRules };
