<template>
  <div class="business-portal-page">
    <portal-header :projectObj="titleConfig"> </portal-header>
    <div class="table-search-container">
      <el-collapse-transition>
        <portal-form
          v-show="collapseValue"
          ref="customerForm"
          :form-data="customerForm"
          :form-config-list="formConfigArray"
          :searchBtnLoading="searchLoading"
          :searchBtnDisabled="!hasPerm('IC_UPDATECUSTOMER_SEARCH_BTN')"
          @reset-btn-click="reset"
          @search-btn-click="search"
          :formRules="customerFormRules"
        />
      </el-collapse-transition>
    </div>
    <div class="table-list-container">
      <!-- 新建 -->
      <el-button
        @click="createCustomer"
        :disabled="!hasPerm('IC_IDENTITY_CREATECUSTOMER_NEXT_BTN')"
        icon="el-icon-plus"
        size="small"
        type="primary"
      >
        {{ $t('common.button.create') }}
      </el-button>
      <!-- 列表 -->
      <table-page
        class="margin-top-16"
        size="small"
        :has-pagination="true"
        :is-search="isSearch"
        :columns="customerColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :sortArr="[
          'status',
          'identityID',
          'msisdn',
          'customerBasicKyc.firstName',
          'customerBasicKyc.middleName',
          'customerBasicKyc.lastName',
          'isVerifySuspended',
        ]"
      >
        <template slot-scope="scope" slot="status">
          <div class="flex flex-align">
            <span
              class="status-circle"
              v-if="scope.data.row.status !== ''"
              :style="statusStyle(scope.data.row.status)"
            ></span>
            <span class="statusStyle">{{ showStatus(scope.data.row.status) }}</span>
            <el-link
              v-if="isAllowCheck(scope.data.row.status) && hasPerm('IC_CHANGE_CUST_STATUS_BTN')"
              :underline="false"
              icon="el-icon-edit"
              @click="editStatus(scope.data.row)"
            ></el-link>
          </div>
        </template>
        <template slot-scope="scope" slot="operation">
          <el-button
            size="mini"
            type="text"
            class="table-operation-btn icon-cursor"
            @click="handleDetail(scope.data.row)"
          >
            {{ $t('common.button.detail') }}
          </el-button>
        </template>
      </table-page>
    </div>
    <edit-status
      @ishow="ishow"
      :closeData="closeData"
      :closeDialog.sync="closeDialog"
      :identityStatus="identityStatus"
      :statusOptions="statusOptions"
      :identityId="customerId"
      @closeSubmit="closeSubmit"
    ></edit-status>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import { queryBusinessCustomerList, getStatusList, getIdType } from '../../../api/api-customer';
import PortalHeader from '@/components/portal-header';
import editStatus from '../components/edit-status';
import { dealApiRes } from '@/views/identifier-center/pages/organization/utils/common.js';
import { customerMutiCondiQueryRules } from '@/views/identifier-center/validator/customer.js';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import { checkFuzzySearchInput } from '@/common/validator/common.js';

export default {
  name: 'CustomerList',
  components: {
    TablePage,
    PortalForm,
    PortalHeader,
    editStatus,
  },
  data() {
    return {
      customerFormRules: customerMutiCondiQueryRules(this),
      closeData: {
        identityId: '',
        tillNumber: '',
      },
      closeDialog: false,
      identityStatus: '', // 弹框Current Identity Status
      scopeIdentityStatus: '', // 弹框Current Identity Status
      customerId: '', // 弹框入参
      total: 0,
      pageNo: 1,
      pageSize: 10,
      // 搜索字段
      customerForm: {
        msisdn: '',
        condition: '',
        firstName: '',
        lastName: '',
        middleName: '',
        idType: '',
        idNumber: '',
        birthday: '',
        customerType: '',
        customerId: '',
      },
      searchLoading: false,
      tills: [],
      titleConfig: {
        title: this.$t('IDC.orgCustomer.customer'),
        desc: this.$t('IDC.orgCustomer.customerDic'),
      },
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      cpId: '', // 手机号码查询才有cpid
      tableList: [], // 列表数据
      customerColumns: [
        {
          label: this.$t('Identity ID'),
          prop: 'identityId',
          'sort-method': (a, b) => {
            return a.identityId > b.identityId ? 1 : -1;
          },
          'min-width': 165,
        },
        {
          label: this.$t('MSISDN'),
          prop: 'msisdn',
          'sort-method': (a, b) => {
            return a.msisdn > b.msisdn ? 1 : -1;
          },
          align: 'left',
          'min-width': 120,
        },
        {
          label: this.$t('First Name'),
          prop: 'customerBasicKyc.firstName',
          'sort-method': (a, b) => {
            return a.firstName > b.firstName ? 1 : -1;
          },
          align: 'left',
          'min-width': 142,
        },
        {
          label: this.$t('Middle Name'),
          prop: 'customerBasicKyc.middleName',
          'sort-method': (a, b) => {
            return a.middleName > b.middleName ? 1 : -1;
          },
          align: 'left',
          'min-width': 142,
        },
        {
          label: this.$t('Last Name'),
          prop: 'customerBasicKyc.lastName',
          'sort-method': (a, b) => {
            return a.lastName > b.lastName ? 1 : -1;
          },
          align: 'left',
          'min-width': 142,
        },
        {
          label: this.$t('Identity Status'),
          prop: 'status',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
          align: 'left',
          __slotName: 'status',
          'min-width': 180,
        },
        {
          label: this.$t('Suspended'),
          prop: 'isVerifySuspended',
          'sort-method': (a, b) => {
            return a.isVerifySuspended > b.isVerifySuspended ? 1 : -1;
          },
          align: 'left',
          'min-width': 150,
        },
        {
          label: this.$t('Customer Type'),
          prop: 'customerType',
          align: 'left',
          'min-width': 150,
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          __slotName: 'operation',
          'min-width': 93,
        },
      ],
      isSearch: false,
      collapseValue: true,
      statusOptions: [], // 状态选项密码默认下拉框
      formConfigArray: [], // 搜素框
      conditionOptions: [
        { label: this.$t('IDC.orgCustomer.MSISDN'), value: 'MSISDN' },
        { label: this.$t('IDC.orgCustomer.firstNameAndLastName'), value: 'firstNameAndLastName' },
        { label: this.$t('IDC.orgCustomer.middleName'), value: 'middleName' },
        { label: this.$t('IDC.orgCustomer.id'), value: 'ID' },
        { label: this.$t('IDC.orgCustomer.dateBirth'), value: 'dateBirth' },
        { label: this.$t('IDC.orgCustomer.identityID'), value: 'IdentityID' },
      ],
      customerTypeOptions: [
        { label: this.$t('IDC.orgCustomer.all'), value: 'All' },
        { label: this.$t('IDC.orgCustomer.registeredCustomer'), value: 'REGISTER' },
        { label: this.$t('IDC.orgCustomer.liteCustomer'), value: 'LITE' },
        { label: this.$t('IDC.orgCustomer.unregisteredCustomer'), value: 'UN_REGISTER' },
      ],
      idTypeOptions: [],
      pickerOptions: {
        disabledDate: (time) => {
          // time <= Date.now()(今天)
          return time.getTime() > Date.now();
        },
      },
    };
  },
  computed: {
    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': '#658DF9' };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
            return { 'background-color': '#658DF9' };
          case '02':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          default:
            return;
        }
      };
    },
    queryParams() {
      const {
        msisdn,
        condition,
        firstName,
        lastName,
        middleName,
        idType,
        idNumber,
        birthday,
        customerType,
        customerId,
      } = this.customerForm;
      let params = {
        page: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
      };
      switch (condition) {
        case 'MSISDN':
          params.msisdn = msisdn;
          break;
        case 'firstNameAndLastName':
          if (firstName && !lastName) {
            params.firstName = firstName;
          } else if (!firstName && lastName) {
            params.lastName = lastName;
          } else if (firstName && lastName) {
            params.firstName = firstName;
            params.lastName = lastName;
          }
          break;
        case 'middleName':
          params.middleName = middleName;
          break;
        case 'ID':
          params.idType = idType;
          params.idNumber = idNumber;
          break;
        case 'dateBirth':
          params.birthday = birthday;
          break;
        case 'IdentityID':
          params.customerType = customerType;
          params.customerId = customerId;
          break;
        default:
          break;
      }
      return params;
    },
    // 搜索数组
    accountColumnsMSISDN() {
      return [
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          label: this.$t('IDC.orgCustomer.queryCondition'),
          key: 'condition',
          comp: 'el-select',
          options: this.conditionOptions,
        },
        {
          label: this.$t('IDC.orgCustomer.MSISDN'),
          key: 'msisdn',
          prop: 'msisdn',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
      ];
    },
    accountColumnsFirstNameAndLastName() {
      return [
        {
          label: this.$t('IDC.orgCustomer.queryCondition'),
          key: 'condition',
          comp: 'el-select',
          options: this.conditionOptions,
        },
        {
          label: this.$t('IDC.orgCustomer.firstName'),
          key: 'firstName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
        {
          label: this.$t('IDC.orgCustomer.lastName'),
          key: 'lastName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
      ];
    },
    accountColumnsMiddleName() {
      return [
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          label: this.$t('IDC.orgCustomer.queryCondition'),
          key: 'condition',
          comp: 'el-select',
          options: this.conditionOptions,
        },
        {
          label: this.$t('IDC.orgCustomer.middleName'),
          key: 'middleName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
      ];
    },
    accountColumnsID() {
      return [
        {
          label: this.$t('IDC.orgCustomer.queryCondition'),
          key: 'condition',
          comp: 'el-select',
          options: this.conditionOptions,
        },
        {
          label: this.$t('IDC.orgCustomer.id'),
          key: 'idType',
          comp: 'el-select',
          options: this.idTypeOptions,
        },
        {
          label: this.$t(' '),
          key: 'idNumber',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
      ];
    },
    accountColumnsDateBirth() {
      return [
        {
          label: this.$t('IDC.orgCustomer.queryCondition'),
          key: 'condition',
          comp: 'el-select',
          options: this.conditionOptions,
        },
        {
          label: this.$t('IDC.orgCustomer.dateBirth'),
          comp: 'el-date-picker',
          key: 'birthday',
          prop: 'birthday',
          class: 'form-special-con',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            type: 'date',
            format: 'yyyy-MM-dd',
            'value-format': 'yyyy-MM-dd',
            'picker-options': this.pickerOptions,
          },
        },
      ];
    },
    accountColumnsIdentity() {
      return [
        {
          label: this.$t('IDC.orgCustomer.queryCondition'),
          key: 'condition',
          comp: 'el-select',
          options: this.conditionOptions,
        },
        {
          label: this.$t('IDC.orgCustomer.identityID'),
          key: 'customerType',
          comp: 'el-select',
          options: this.customerTypeOptions,
        },
        {
          label: this.$t(' '),
          key: 'customerId',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
      ];
    },
  },
  watch: {
    'customerForm.condition': {
      handler(val) {
        this.$nextTick(() => {
          this.$refs['customerForm'].innerFormRef.clearValidate();
        });
        switch (val) {
          case 'MSISDN':
            this.customerForm.msisdn = '%';
            this.formConfigArray = this.accountColumnsMSISDN;
            break;
          case 'firstNameAndLastName':
            this.customerForm.firstName = '%';
            this.customerForm.lastName = '%';
            this.formConfigArray = this.accountColumnsFirstNameAndLastName;
            break;
          case 'middleName':
            this.customerForm.middleName = '%';
            this.formConfigArray = this.accountColumnsMiddleName;
            break;
          case 'ID':
            this.customerForm.idNumber = '';
            this.formConfigArray = this.accountColumnsID;
            break;
          case 'dateBirth':
            this.formConfigArray = this.accountColumnsDateBirth;
            break;
          case 'IdentityID':
            this.customerForm.customerId = '';
            this.formConfigArray = this.accountColumnsIdentity;
            break;

          default:
            break;
        }
      },
      immediate: true,
      deep: true,
    },
  },
  created() {
    this.customerForm.condition = this.conditionOptions[0].value;
    this.initPanel();
  },
  methods: {
    async initPanel() {
      const result = await dealApiRes(getIdType, 'lists', {});
      if (result) {
        this.idTypeOptions = result.map((item) => {
          return {
            label: item.defaultName,
            value: item.value,
          };
        });
      }
    },
    showCustomerType(val) {
      switch (val) {
        case 1000:
          return 'Unregistered Customer';
        case 1001:
          return 'Lite Customer';
        case 1002:
          return 'Registered Customer';
        default:
          return;
      }
    },
    // 弹出弹框
    editStatus(scope) {
      this.identityStatus = this.showStatus(scope.status);
      this.scopeIdentityStatus = scope.status;
      this.customerId = scope.identityId;
      this.statusOptionsFn();
    },
    showColose(scope) {
      this.closeData = {
        identityId: scope.identityId,
        tillNumber: scope.tillNumber,
      };
      this.closeDialog = true;
    },
    ishow() {
      this.statusOptions = [];
      this.closeDialog = false;
    },
    closeSubmit() {
      this.isSearch = true;
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
    reset() {
      this.customerForm = {
        msisdn: '',
        condition: this.conditionOptions[0].value,
        firstName: '',
        lastName: '',
        middleName: '',
        idType: '',
        idNumber: '',
        birthday: '',
        customerType: '',
        customerId: '',
      };
      this.isSearching = false;
    },
    search() {
      this.paginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
      this.$refs['customerForm'].innerFormRef.validate((valid) => {
        if (valid) {
          const { condition, firstName, lastName } = this.customerForm;
          const checkObj = {
            firstName,
            lastName,
          };
          if (condition === 'firstNameAndLastName' && !checkFuzzySearchInput(checkObj)) {
            this.$confirm('Specify at least one query condition.', 'Confirm', {
              confirmButtonText: 'Confirm',
              showCancelButton: false,
              closeOnClickModal: false,
              type: 'warning',
              customClass: 'box-logout',
            }).then(async () => {
              this.searchLoading = false;
              this.isSearch = false;
            });
          } else {
            this.isSearch = true;
          }
        } else {
          this.searchLoading = false;
          return false;
        }
      });
    },
    handleDetail(row) {
      let params = {
        identityID: row.identityId,
        cpId: this.cpId,
      };
      this.openNewWindow({ path: 'detail', query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    createCustomer() {
      this.openNewWindow({ path: 'create' });
    },
    async getTableData() {
      const res = await queryBusinessCustomerList({ ...this.queryParams });
      this.searchLoading = false;
      this.isSearch = false;
      this.refreshTableFlag = false;
      try {
        if (res.header && res.header.success) {
          let list = res.customerList.slice(0);
          res.list = list.map((row) => {
            return Object.assign(row, {
              isVerifySuspended: row.isVerifySuspended ? 'Yes' : 'No',
              customerType: this.showCustomerType(row.customerType),
            });
          });
          res.totalRecords = res.total;
        }
        return res;
      } catch (e) {
        return {
          header: {
            success: false,
          },
        };
      }
    },
    statusOptionsFn() {
      return new Promise((resolve) => {
        let params = {};
        params.status = this.scopeIdentityStatus;
        params.customerId = this.customerId;
        getStatusList(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.total;
              res.nextStatusList.map((statu) => {
                let obj = {};
                obj.value = statu;
                obj.label = this.showStatus(statu);
                this.statusOptions.push(obj);
              });
              this.closeDialog = true;
            }
            resolve(this.statusOptions);
          })
          .catch(() => {})
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
    isAllowCheck(status) {
      return ['03', '04', '05'].includes(status);
    },
  },
};
</script>
<style scoped lang="scss">
.business-portal-page {
  // 状态映射
  .status-circle {
    width: 6px;
    height: 6px;
    border-radius: 100%;
    display: inline-block;
    margin-right: 4px;
  }
  /deep/ .form-flex-container .row-flex-wrap {
    flex-wrap: nowrap;
  }
  /deep/ .form-flex-container .el-form-item--small .el-form-item__label {
    height: 18px;
  }
}
</style>
