/**
 *
 * @param {Function<Promise>} fnApi 请求api方法
 * @param {String} receivedField 需要用的字段名称
 * @param  {...any} arg fnApi的请求参数
 * @returns
 */
export const dealApiRes = (fnApi, receivedField, ...arg) => {
  return fnApi(...arg)
    .then((res) => {
      if (res.header && res.header.success) {
        return res[receivedField];
      }
      return null;
    })
    .catch(() => {
      return null;
    });
};

/**
 *
 * @param {Function<Promise>} fnApi 请求api方法
 * @param {String} receivedField 需要用的字段名称
 * @param  {...any} arg fnApi的请求参数
 * @returns
 */
export const dealApiResAndParse = (fnApi, receivedField, ...arg) => {
  return dealApiRes(fnApi, receivedField, ...arg)
    .then((res) => {
      if (!res) {
        return null;
      }
      return JSON.parse(res);
    })
    .catch((err) => {
      return null;
    });
};

/**
 *  以key-value的形式执行 Promise.all，得到对应结构的结果
 * @param {{[String]: Function<Promise>}} fnMap
 * @returns
 */
export const getbatchData = (fnMap) => {
  const keys = Object.keys(fnMap);
  const fetchFns = Object.values(fnMap);

  return Promise.all(fetchFns).then((res) => {
    const resultEntries = keys.map((key, index) => {
      return [key, res[index]];
    });
    return Object.fromEntries(resultEntries);
  });
};

/**
 * 把一个list转换成 select options 结构
 * @param {String} valueField 原数据中value对应的字段
 * @param {String} labelField 原数据中label对应的字段
 * @param {Boolean} isPure 是否只返回 label，value两个字段
 * @returns {{lable:String, value:any}}
 */
export const transOptions = (valueField, labelField, isPure = false) => {
  return (item) => {
    const option = {
      label: item[labelField],
      value: item[valueField],
    };

    if (isPure) {
      return option;
    }

    return {
      ...item,
      ...option,
    };
  };
};

/**
 * 从源list数据中获取对应字段的一项
 * @param {Array<Object>} list 源数据
 * @param {String} field 通过该字段取值
 * @param {any} value
 * @returns
 */
export const getItemFromListByField = (list, value, field) => {
  const item = list.find((e) => e[field] === value);
  return item;
};

/**
 * 从源list数据中获取对应字段的一项的label
 * @param {Array<Object>} list 源数据
 * @param {String} field 通过该字段取值
 * @param {any} value
 * @returns
 */
export const getLabelFromOptions = (list, value) => {
  const item = getItemFromListByField(list, value, 'value') || {};
  return item['label'];
};

export const deepClone = (sourceData, cache) => {
  if (!cache) {
    cache = new Map();
  }
  if (!(sourceData instanceof Object)) {
    return sourceData;
  }
  //  iframe 不考虑跨
  if (cache.get(sourceData)) {
    return cache.get(sourceData);
  }
  let obj;
  if (sourceData instanceof Function) {
    if (sourceData.prototype) {
      // 函数
      obj = function () {
        return sourceData.apply(this, arguments);
      };
    } else {
      obj = (...args) => {
        return sourceData.call(undefined, ...args);
      };
    }
  } else if (sourceData instanceof Array) {
    obj = [];
  } else if (sourceData instanceof Date) {
    obj = new Date(sourceData - 0);
  } else if (sourceData instanceof RegExp) {
    obj = new RegExp(sourceData.sourceData, sourceData.flags);
  } else {
    obj = {};
  }
  cache.set(sourceData, obj);
  for (let key in sourceData) {
    if (Object.hasOwnProperty.call(sourceData, key)) {
      obj[key] = deepClone(sourceData[key], cache);
    }
  }
  return obj;
};
